const iconPlus = document.querySelector('.footer__accordeon-icon--plus');
const iconMinus = document.querySelector('.footer__accordeon-icon--minus');
const accordeons = document.querySelectorAll('.js-accordion');

function findElements(object, element) {
  const instance = object; //  3   
  instance.element = element; //  4       (   js-accordion, )
  instance.target = element.nextElementSibling; //  5         ,        js-accordion (    css  + ).           js-accordion    answer.
}

function hideElement(object) {
  for (let i = 0; i < accordeons.length; i++) {
    accordeons[i].style.height = 'null';
    iconMinus.style.display = 'none';
    iconPlus.style.display = 'block';
  }
  const instance = object;
  const {target} = instance;
  target.style.height = null;
  instance.isActive = false;
}

function showElement(object) {
  for (let i = 0; i < accordeons.length; i++) {
    accordeons[i].style.height = 'null';
    iconMinus.style.display = 'block';
    iconPlus.style.display = 'none';
  }
  const instance = object;
  const {target, height} = instance;
  target.style.height = `${height}px`; //  10  answer   
  instance.isActive = true; //  11     (  )       ()
}

function changeElementStatus(instance) {
  if (instance.isActive) { //  12     2-     true
    hideElement(instance);
  } else {
    showElement(instance); //  9            
  }
}

function measureHeight(object) {
  const instance = object; //  6     ,       findElements
  instance.height = object.target.firstElementChild.clientHeight; //  7    answer
}

function subscribe(instance) {
  instance.element.addEventListener('click', (event) => { //  8   js-accordion   
    event.preventDefault();
    changeElementStatus(instance);
  });
  window.addEventListener('resize', () => measureHeight(instance)); //      (  )        target (  answer)
}

function accordion(element) { //       
  const instance = {};

  function init() {
    closeAccordions();
    findElements(instance, element);
    measureHeight(instance);
    subscribe(instance);
  }

  init();
}

function closeAccordions() {
  for (let i = 0; i < accordeons.length; i++) {
    accordeons[i].style.height = 'null';
  }
}

const elements = [...document.querySelectorAll('.js-accordion')]; //  1     
const accordionRealisation = elements.forEach(accordion); //  2      accordion

export {accordionRealisation};
